import "./Announcement.css";

import image_announcement_svg from "./images/imagen_anuncio.svg";

const Announcement = () => {
    return (
        <div className="class_announcement_container">
            <div className="class_announcement">
                <div className="class_announcement_description">
                    <span className="class_announcement_title">
                        Fcil, Rpido y Preciso!
                    </span>

                    <span className="class_announcement_subtitle">E-commerce, ahora todo es ms sencillo!</span>

                    <span className="class_announcement_text_body">
                        Desde hace tiempo, la compra de productos, algo esencial en nosotros,
                        se convirti en un problema tanto para compradores como para vendedores, varios factores jugaban en contra de ambos. 
                        Distancia, clima, y otras caractersticas, determinaban el da a da del comprador como del vendedor. Hoy en da, gracias a la tecnologa
                        y a su avance, el comercio se volvi ms accesible y ms prctico, convirtindose en un gran impacto a nivel mundial. 
                        La mayor parte del comercio electrnico consiste 
                        en la compra y venta de productos o servicios entre personas y empresas.
                    </span>
                </div>

                <div className="class_announcement_image_container">
                    <img src={image_announcement_svg} className="class_img_announcement"/>
                </div>
            </div>
        </div>
    );
}

export default Announcement;